.root {
  display: flex;
  flex-direction: row;
  align-items: center;

  .title {
    min-width: 30%;
    border-radius: 10px;
    padding: 5px;
    text-align: center;
    margin: 5px;
    background-color: grey;
    color: white;
    font-style: bold;
  }

  .input {
    display: flex;
    flex-direction: row;

    align-items: center;
    width: 100%;

    .label {
      margin-right: 10px;
      margin-left: 10px;
    }

    div[class*='root'] {
      margin-bottom: unset;
    }
  }
}
